import React, {useEffect} from 'react';
import main from '../static/images/main.jpg'
import data from '../test';
import StaticProject from '../Components/StaticProject';
import './designs.css'


const Designs = (props) => {

    useEffect(() => {
        setTimeout(function() {
            document.getElementById("top-image").style.opacity = 1;
            //document.getElementById("main-image").style.opacity = 1;
            document.getElementById("main-image-text").style.opacity = 1;
        }, 1000)
    }, [])

    return (
        <div>
            <div id="top-container">
               <div id="top-image">
                    {/* <img id="main-image" src={main} alt="Main" /> */}
                    <div id="main-image-text">
                        Recent Projects
                    </div>
               </div>
               <hr style={{width: '75%', height: '30px', backgroundColor: '#707070'}}/>
            </div>
            <div id="project-grid">
                <div id="project-grid-1"></div>
                <div id="project-grid-2"></div>
                <div id="project-grid-3"></div>
                <div id="project-grid-4"></div>
                <div id="project-grid-5"></div>
                <div id="project-grid-6"></div>
                <div id="project-grid-7"></div>
            </div>
            <div style={{width: '75%', margin: 'auto'}}>
            <h3 id="top-container-header">Past Projects</h3>
                {data.Projects.map(project => {
                    return <StaticProject key={project.id} images={project.images} description={project.description} />
                })}
            </div>
        </div>
    )
}

export default Designs;